<script>

import {list,dely} from "@/api/fix/listy";
import {save,del} from "@/api/fix/list";
import security from '@/utils/security'
export default {
  data() {
    return {
      data: [],
      formData: {
        model: localStorage.getItem("model"),
      },
      role: security.getUser(),
    };
  },
  methods: {
    findSubmit(){
      list(this.formData).then((res) => {
        this.data = res.data;
      });
    },
    sub(data){
      if(data.status=='2'){
        var listyid=data.id;
        data.id=data.listid;
        save(data).then((res) => {
            if (res.code == 200&&data.id===undefined) {
            this.$message.success("添加成功");
            }
            else{
            this.$message.success("修改成功");
            }
        });
        dely(listyid).then((res) => {
          this.findSubmit()
        })
      }
      else{
        del(data.listid).then((res) => {
          this.findSubmit()
        })
        this.$message.success("刪除成功");
        dely(data.id).then((res) => {
          this.findSubmit()
        })
      }
    },
    delhcy(id){
      dely(id).then((res) => {
        this.findSubmit()
      })
    }

  },
  created: function () {
    list(this.formData).then((res) => {
      this.data = res.data;
    });
  },
};
</script>